input:focus{
  border-color: #355463;
  outline-color: #6c7a94;
  outline-offset: 0;
}

button {
  padding: 0 18px;
  height: 29px;
  font-size: 12px;
  font-weight: bold;
  color: #42809c;
  text-shadow: 0 1px #85a6aa;
