  border: 1px solid;
  border-color: #c5dff1 #bedfee #95cfdd;
  border-radius: 16px;
  outline: 0;
  box-sizing: content-box;
  background-image: linear-gradient(to bottom, #709cb6, #81a8be);
  box-shadow: inset 0 1px white, 0 1px 2px rgba(0, 0, 0, 0.15);
}
button:active {
  background: #93a4be;
